import React from 'react';
import { ReturnProject } from './ReturnProject';
import candyCrushImg  from '../../../Img/candyCrushImg.png';
import gifAppImg  from '../../../Img/gifAppImg.png';
import todoAppImg  from '../../../Img/todoAppImg.png';
import calendarAppImg  from '../../../Img/calendarAppImg.png';
import { useTranslation } from 'react-i18next';

export const ProjectsScreen = () => {
    const [ t ] = useTranslation('global');

    return (
        <div id='projects' className='screen'>
            
            <h1>{ t('projects.Title') }</h1>
            <ReturnProject
                title='CandyCrush' 
                projectImg={ candyCrushImg }
                side='right'
                codeLink='https://github.com/aleLa11i/candyCrush.git'
                repoLink='https://alela11i.github.io/candyCrush/'
                text={ t('projects.CandyCrushText') }
            />
            <ReturnProject
                title='GifApp' 
                projectImg={ gifAppImg }
                side='left'
                codeLink='https://github.com/aleLa11i/gifapp.git'
                repoLink='https://alela11i.github.io/gifapp/'
                text={ t('projects.GifAppText') }
            />
            <ReturnProject
                title='TodoApp' 
                projectImg={ todoAppImg }
                side='right'
                codeLink='https://github.com/aleLa11i/TODOapp.git'
                repoLink='https://alela11i.github.io/TODOapp/'
                text={ t('projects.TodoAppText') }
            />
            <ReturnProject
                title='CalendarApp' 
                projectImg={ calendarAppImg }
                side='left'
                codeLink='https://github.com/aleLa11i/mern-calendar-frontend.git'
                repoLink='https://mern-calendar-alelalli.herokuapp.com/'
                text={ t('projects.CalendarAppText') }
            />

            
        </div>
    )
}
