import React from 'react';
import { useTranslation } from 'react-i18next';

export const ReturnProject = ({ projectImg, text, title, side, codeLink, repoLink }) => {
    const [ t ] = useTranslation("global");

    return (
    <div
        className={`project ${side}`}
    >
        <div
            className='project-item-background'
        >
            <img 
                className='project-background'
                src={ projectImg }
                alt='project-img' 
            />
        </div>  
        <div
            className='project-item'
        >
            <div
                className='project-description'
            >  
                <div>
                    <h1>
                        { title }
                    </h1>
                    <p 
                        className="text"
                    >
                        {text}
                    </p>
                    <a
                        className="btn"
                        target="_blank"
                        href={ repoLink } 
                    >
                        { title }
                    </a>
                    <a
                        className="btn"
                        target="_blank"
                        href={ codeLink }
                    >
                        { t("projects.Code") }
                    </a>
                </div>      
            </div>

            <img 
                className='notebook'
                src='https://imagenesgratis.com.ar/wp-content/uploads/2020/02/notebook-laptop-compu-png-transparent-design-sin-fondo.png'
                alt='notebook' 
            />
            <img 
                className='project-img'
                src={ projectImg }
                alt='project-img' 
            />
        </div>

    </div>
  )
}
