import React, { useEffect } from 'react';
import { sendData } from '../../actions/contact-form';
import { useForm } from '../Hooks/useForm';
import { useTranslation } from 'react-i18next';

const initialState = JSON.parse(localStorage.getItem('contact-form-data')) 
||  {
        email:'',
        title:'',
        text:''
    }

export const ContactScreen = () => {
    const [ t ] = useTranslation('global');
    const [value,handleInputChange,reset] = useForm(initialState)
    const {email,title,text} = value;

    useEffect(() => {
        localStorage.setItem('contact-form-data',JSON.stringify(value));
    }, [value])

    const handleReset = (e) => {
        e.preventDefault();
        reset({
            email:'',
            title:'',
            text:''
        })
    }

    const handleSend = (e) => {
        e.preventDefault();
        sendData(value);
    }

    return (
        <div id='contact' className='contact screen'>
            <h1> { t('contact.Title') } </h1>
            
            <form 
                className='contact-form'
                onCharge={handleSend}
            >
                
                <input
                value={email}
                type='text'
                className='contact-item'
                name='email'
                placeholder={ t('contact.Placeholder1') }
                onChange={handleInputChange}

                >
                </input>
                <input
                value={title}
                type='text'
                className='contact-item '
                name='title'
                placeholder={ t('contact.Placeholder2') }
                onChange={handleInputChange}
                >
                </input>
                
                <textarea
                value={text}
                className='contact-item '
                name='text'
                placeholder={ t('contact.Placeholder3') }
                onChange={handleInputChange}
                >
                </textarea>
                <div>
                    <button
                        className='contact-item hover btn btn-send'
                        onClick={handleSend}
                    >
                        { t('contact.ButtonSend') }
                    </button>
                    <button
                        className='btn contact-item hover'
                        onClick={handleReset}
                        >
                            { t('contact.BleachFields') }
                    </button>
                </div>
            </form>
        </div>
    )
}
