import React from 'react';

export const ReturnSkills = ( { Name, Img } ) => {
  return (
    <div className='skills-image'>
        <img 
        src={'https://res.cloudinary.com/dxzcqv92g/image/upload/'+ Img}
        alt='skills-image'
        />
        <h3> { Name } </h3>
    </div> 
  )
}
