import React from 'react';
import { useTranslation } from 'react-i18next';

export const AboutMeScreen = () => {
    const [ t ] = useTranslation('global');

    return (
        <div id='aboutme' className='aboutme screen'>        
            
            <h1> { t('about.Title') } </h1>
            <div className='image-face'>
                <img
                    src='https://res.cloudinary.com/dxzcqv92g/image/upload/v1633148060/ouyct1etzybgianjydzo.jpg'
                    alt='aboutme-face' 
                />
            </div>
            
            <p>{ t('about.Text1') }</p>  
            <p>{ t('about.Text2') }</p>  
            <p>{ t('about.Text3') }</p>  
            <p>{ t('about.Text4') }</p>    
        </div>
    )
}
