 import React, { useCallback, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { NavBarItems } from './NavBarItems';

export const NavBarScreen = () => {

  const [t, i18n] = useTranslation('global');
  const { changeLanguage } = i18n
  const [showNavBar, setShowNavBar] = useState('collapse navbar-collapse')
  
  const listItems = [
    ['#home', t('sections.Home') ],
    ['#aboutme', t('sections.About')],
    ['#skills', t('sections.Skills')],
    ['#projects', t('sections.Projects')],
    ['#contact', t('sections.Contact')],
  ]

  const setLanguage = ( language ) =>{
    
    changeLanguage( language );

    localStorage.setItem('language', language);
  };

  const show = useCallback(
      () => {
          if( showNavBar === 'collapse navbar-collapse' ){
            setShowNavBar('collapse navbar-collapse show')
          }
          else
          {
            setShowNavBar('collapse navbar-collapse')
          }
    }, [ showNavBar ],)

return (

  <div
    className='fixed-top animate__animated animate__fadeInLeft'
  >
      <nav className='navbar navbar-expand-lg navbar-dark bg-dark'>
        <div className='container-fluid'>
          
          <img 
            height='40'
            src='https://res.cloudinary.com/dxzcqv92g/image/upload/v1634078458/kjeycctakluagocmvjwi.png' 
            alt='img-logo'
          />
  
          <button 
            className='navbar-toggler' 
            onClick={ show }
            type='button' 
            >
            <span 
              className='navbar-toggler-icon'
            ></span>
          </button>

          <div 
            className={ showNavBar }
            id='navbarSupportedContent'
          >
            <ul className='navbar-nav me-auto mb-2 mb-lg-0'>

            {
              listItems.map( item =>{
                return <NavBarItems key={ item[0] } href={ item[0] } text={ item[1] } show={ show } />
              })
            }

              <li
                className='language m-3 d-flex nav-item'
                style={{'color':'white'}}
                onClick={ show }
              >
                <div
                  onClick={ ()=> setLanguage('es') }
                >ES</div>
                /
                <div
                  onClick={ ()=> setLanguage('en') }
                >EN</div>
              </li>

            </ul>

          </div>

        </div>

      </nav>
  </div>
)
}
