import React, { useEffect, useState } from 'react';
import { Intro } from './Components/Portfolio/Intro/Intro';
import { PortfolioScreen } from './Components/Portfolio/PortfolioScreen';
import 'animate.css';

const time = 3000;

export const App = () => {

    const [introFade, setIntroFade] = useState('');
    const [fade, setFade] = useState(false)

    useEffect(() => {
        if( fade ){
        console.log(fade)
        setIntroFade( 'animate__slideOutUp' )
        setTimeout(() => {
            setIntroFade('intro-out')
        }, 1000); 
        }
    }, [ fade ])

    useEffect(() => {
        setTimeout(() => {
        setFade(true)
        }, time)
    }, [])

    return (
    <>
        <Intro introFade={ introFade } setFade={ setFade } />
        {
            fade
                &&
            <PortfolioScreen fade={ fade } />   
        }
    </>
    )
}
