import React from 'react';

export const NavBarItems = ({ href, text, show }) => {

  return (
    <li 
        className='nav-item'
        onClick={ show }
        >
        <a 
            activeclassname='active'
            className='nav-item nav-link active' 
            href={ href }
        >
            { text }
        </a>
    </li>
  )
}
