import React from 'react';

export const Intro = ({ introFade, setFade }) => {

  return (
    <div  
      className={'intro animate__animated '+introFade}
      onClick={ ()=> setFade(true) } 
    >
        <h1
          className={'animate__animated animate__zoomIn'}
        >Welcome!</h1>
        <h5
          className={'animate__animated animate__zoomIn'}
        >
          I'm Alesandro Lalli, Frontend Developer
        </h5>
        <h4>
          Click to omit
        </h4>
    </div>
  )
}
