import React from 'react';
import { ReturnSkills } from './ReturnSkills';
import { useTranslation } from 'react-i18next';

const skills =[
    { name:'HTML',          img: 'v1633743644/cwmzv6yted23xb0zjudk.png' },
    { name:'CSS',           img: 'v1633743576/rikhwq96tscxgm9bo9lm.png' },
    { name:'Javascript',    img: 'v1633745658/gx7eg06bqwyo5xh6veg7.png' },
    { name:'NodeJS',        img: 'v1633743225/xpzjk4p6ywfkdzihskqh.png' },
    { name:'React',         img: 'v1633742339/wmcoslgcvokzy92dyr7f.png' },
    { name:'Git',           img: 'v1633742584/bpgrq09eqkxapfdskaih.png' },
    { name:'MongoDB',       img: 'v1633742963/m1bqjamypf4i16x3ca9i.png' },
    { name:'SASS',          img: 'v1633742618/koddr0votogm7ovc1vsl.png' },
    { name:'Boostrap',      img: 'v1633742658/gf4pdw0ohitshsrpatfk.png' },
]

export const SkillsScreen = () => {
    const [t] = useTranslation('global');

    
    return (
        
        <div id='skills' className='screen skills'>
            <h1>{ t('skills.Title') }</h1> 
            <div>
                {
                    skills.map( ( skill, index ) => {
                        return (
                        <ReturnSkills 
                            Key={ index }  
                            Name={ skill.name } 
                            Img={ skill.img } 
                        />)
                    })
                }
            </div>
        </div>
    )
}








